.part-2-lenstart{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    /* margin: 20px; */
    width: 100%;
}


.part-2-content-1,.part-2-content-2,.part-2-content-3{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    width: 100%;
    margin: 10px;
}

.part-2-content-1-img{
    margin: 10px;
    width:45%;
}

.part-2-content-1-img img{
    width:75%;
    border-radius: 10px;
    /* box-shadow: -14px 8px 6px rgb(12, 12, 70); */
}

.part-2-content-2-img{
    width:50%;
    margin:10px;
}

.part-2-content-2-img img{
    width:90%;
    border-radius: 10px;
    /* box-shadow: -14px 8px 6px rgb(12, 12, 70);    */
}

.part-2-content-3-img{
    width:40%;
    margin:10px;
}

.part-2-content-3-img img{
    width:100%;
    border-radius: 10px;
    /* box-shadow: -14px 8px 6px rgb(12, 12, 70);    */
}

.part-2-content-1-content,.part-2-content-3-content,.part-2-content-2-content-1,.part-2-content-2-content-2{
    width:30%;
    box-sizing: border-box;
    padding: 30px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-left: 30px;
    border: 1px solid rgb(12, 12, 70);
    border-radius: 10px;
}

.part-2-content-1-content div{
    font-size: 25px;
    box-sizing:border-box;
    font-weight: bold;
}

.part-2-content-1-content span{
    font-weight: bold;
}


.part-2-content-2-content{
    width:40%;
    box-sizing: border-box;
    padding: 30px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    margin-left: 30px;
}

.part-2-content-2-content-1,.part-2-content-2-content-2{
    width: 85%;
    box-sizing:border-box;
    margin: 10px;
}

.part-2-content-2-content-1 div,.part-2-content-2-content-2 div{
    font-size: 25px;
    font-weight: bold;
    box-sizing:border-box;
}

.part-2-content-2-content-1 span,.part-2-content-2-content-2 span{
    font-weight: bold;
}
.part-2-content-3-content div{
    font-size: 25px;
    box-sizing:border-box;
    font-weight: bold;
}
.part-2-content-3-content span{
    font-weight: bold;
}


@media only screen and (max-width:800px){
    .part-2-content-1-img,.part-2-content-2-img,.part-2-content-3-img{
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-bottom: 20px;
    } 
    .part-2-content-1-img img,.part-2-content-2-img img,.part-2-content-3-img img{
        width: 90%;
        box-shadow: none;
    }
    .part-2-content-2-content{
        width: 100%;
        padding: 0px;
        margin-left: 0;
    }
    .part-2-content-2-content-1,.part-2-content-2-content-2{
        width: 100%;
        /* padding: 0px; */
    }  
    .part-2-content-1-content,.part-2-content-3-content,.part-2-content-2-content-1,.part-2-content-2-content-2{
        width: 100%;
        margin-left: 10px;
    }
    .part-2-content-1,.part-2-content-3{
        padding: 0px;
        
    }
}